
import { defineComponent, inject, ref, computed } from 'vue';
import Modal from '@/components/Modal.vue';
import Dropdown from '@/components/Dropdown.vue';
import ItemDetail from '@/components/ItemDetail.vue';
import { Datastore, Item, Unit } from '@/datastore';
import { Option } from '@/utils';

export default defineComponent({
  components: {
    Modal,
    Dropdown,
    ItemDetail,
  },
  setup () {
    const datastore = inject<Datastore>('datastore')!
    const units = ref<Unit[]>([])
    datastore.unit.getAllUnits()
      .then(x => {
        units.value = x
      })
    const items = ref<Item[]>([])
    datastore.item.getAllItems()
      .then(x => {
        items.value = x
      })

    const unitOptions = computed((): Option[] => units.value.map(x => ({ value: x.id!, text: x.name })))
    const itemTableData = computed(() => items.value.map(x => {
      return {
        code: x.code,
        name: x.name,
        unitName: units.value.find(y => y.id === x.unitID)?.name,
        price: x.price,
      }
    }))

    const selectedIndex = ref<number>()
    const selectedItem = computed(() => selectedIndex.value || selectedIndex.value === 0 ? items.value[selectedIndex.value] : {
      code: '-',
      name: '-',
      price: 0,
    })

    const formData = ref<Item>({
      code: '',
      name: '',
      unitID: undefined,
      price: 0,
    })
    const modalState = ref(false)
    const modalHeading = ref('')
    function openAdd() {
      formData.value = {
        code: '',
        name: '',
        unitID: undefined,
        price: 0,
      }
      modalHeading.value = 'เพิ่มสินค้า'
      modalState.value = true
    }
    function openEdit() {
      formData.value = {
        ...items.value[selectedIndex.value!]
      }
      modalHeading.value = 'แก้ไขสินค้า'
      modalState.value = true
    }
    function onSubmit() {
      const data = formData.value
      if (data.id && (!data.code || data.code.length === 0)) {
        alert('code is empty')
        return
      }
      if (!data.name || data.name.length === 0) {
        alert('name is empty')
        return
      }
      if (data.price === undefined || data.price === null) {
        alert('price is empty')
        return
      }
      if (!data.unitID) {
        alert('unitID is empty')
        return
      }

      datastore?.item.editItem(formData.value)
        .then(async () => {
          items.value = await datastore.item.getAllItems()
          modalState.value = false
        })
        .catch(x => alert(x))
    }
    function onDelete() {
      datastore?.item.deleteItem(items.value[selectedIndex.value!].id!)
        .then(async () => items.value = await datastore.item.getAllItems())
        .catch(x => alert(x))
    }

    const prevDisabled = computed(() => (!selectedIndex.value && selectedIndex.value !== 0) || selectedIndex.value === 0)
    function prev() {
      if (prevDisabled.value)
        return

      selectedIndex.value!--
    }
    const nextDisabled = computed(() => (!selectedIndex.value && selectedIndex.value !== 0) || selectedIndex.value === items.value.length - 1)
    function next() {
      if (nextDisabled.value)
        return

      selectedIndex.value!++
    }

    return {
      unitOptions,
      itemTableData,
      selectedIndex,
      selectedItem,
      formData,
      modalState,
      modalHeading,
      openAdd,
      openEdit,
      onSubmit,
      onDelete,
      prevDisabled,
      prev,
      nextDisabled,
      next,
    }
  },
});
